@article-image-slim-url: url("../img/slim-cat-mobile.svg");
@article-image-pro-url: url("../img/relief-cat-mobile.svg");

.articles {
  padding: 20px;
  padding-top: 25px;
  padding-bottom: 10px;

  @media (min-width: @tablet-width) {
    padding: 30px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 80px;
    align-items: stretch;
    padding: 110px;
    padding-top: 82px;
    padding-bottom: 56px;
  }
}

.articles__article {
  position: relative;
  background-color: #f2f2f2;
  padding: 20px;
  padding-top: 30px;
  padding-bottom: 18px;

  @media (max-width: @tablet-width - 1) {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  @media (min-width: @tablet-width) and (max-width: @desktop-width - 1) {
    padding: 53px;
    padding-top: 77px;

    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 0;
    padding: 52px;
    padding-top: 77px;
    padding-bottom: 55px;
  }
}

.articles__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: #000000;
  margin: 0;
  margin-left: 71px;
  margin-bottom: 29px;

  &::before {
    position: absolute;
    content: "";
    background-repeat: no-repeat;
    background-position: center;
    transform: translateX(-78px) translateY(-18%);
    width: 67px;
    height: 53px;
  }

  @media (min-width: @tablet-width) and (max-width: @desktop-width - 1) {
    max-width: 340px;
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 24px;
    margin-left: 0;

    &::before {
      position: absolute;
      top: 35%;
      right: -7%;
      width: 268px;
      height: 211px;
      background-size: contain;
    }
  }

  @media (min-width: @desktop-width) {
    max-width: 305px;
    font-size: 36px;
    line-height: 36px;
    margin-left: 162px;
    margin-bottom: 73px;

    &::before {
      transform: translateX(-181px) translateY(-35%);
      width: 134px;
      height: 106px;
      background-size: contain;
    }
  }
}

.articles__title--slim {
  &::before {
    background-image: @article-image-slim-url;
  }
}

.articles__title--pro {
  &::before {
    background-image: @article-image-pro-url;
  }
}

.articles__text {
  margin: 0;
  margin-bottom: 15px;
  padding-bottom: 23px;
  border-bottom: 1px solid #d9d9d9;

  @media (min-width: @tablet-width) {
    max-width: 340px;
    color: #000000;
    padding: 0;
    margin-bottom: 48px;
    margin-left: 0;
    border: none;
  }

  @media (min-width: @desktop-width) {
    max-width: 467px;
    margin-bottom: 27px;
    padding: 0;
    border: none;
  }
}

.articles__link {
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
  padding-top: 4px;
  padding-bottom: 4px;
  padding-right: 58px;

  &::after {
    position: absolute;
    content: "";
    background-image: url("../img/article-link-icon.svg");
    background-repeat: no-repeat;
    background-position: center;
    transform: translateX(17px);
    width: 24px;
    height: 17px;
  }

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 30px;
    margin-top: auto;
    margin-bottom: 0;

    &::after {
      transform: translateX(25px);
      width: 24px;
      height: 30px;
    }
  }

  @media (min-width: @desktop-width) {
    &::after {
      transform: translateX(28px);
      width: 24px;
      height: 30px;
    }
  }
}
